import React, {useState} from 'react';
import Button from '../UI/Button';
import classes from './UsersList.module.css';
import ErrorModal from '../UI/ErrorModal';
// import Card from '../UI/ErrorModal';


const ListItem = (props) =>{

const[del,setDel] = useState(false);

    const deleteHandler = () => {
      setDel(true);
        // console.log("ListItem ki id"+props.id);
        // props.onDelete(props.id);
      };

      const errorHandler = () => {
        props.onDelete(props.id);
        setDel(false);
      }

      const editHandler = () => {
        console.log("ListItem ki id"+props.id);
        props.onEdit(props.id);
      };
    const text = "Do you really want to delete";
    const message = "You will not able to get data back";
      return (
        <li className = {classes.liitem} >
          
          <div>{props.children}</div>
          <div><Button onClick={deleteHandler}>Dlt</Button>
          <Button onClick={editHandler}>Ed</Button></div>
          {del && <ErrorModal title={text} message={message} onConfirm = {errorHandler}/>}
          
        </li>
      );
}

export default ListItem;